import axios from 'axios'

// 
axios.interceptors.request.use(config => {
  // 1. 
  // 2.  
  return config
}, error => {
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(response => {
  // 
  // 1. 
  // 2. 
  return response
}, error => {
  // 
  if (error && error.response) {
    switch (error.response.status) {
      case 400:
        // 400\
        break
      case 401:
        //  401 
        break
      default:
        // 
        return Promise.reject(error)
    }
  }
})
